/*
Reset.css
Updated: 2021-08-25
Author: Youni
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center, dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
button, input, select, textarea {
  font-size: 100%;
  margin: 0;
  vertical-align: baseline;
}

button, input {
  line-height: normal;
}

:focus {
  outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul, li {
  list-style: none;
}

li {
  display: list-item;
}

table {
  display: table;
  border-collapse: collapse;
  border-spacing: 0;
}

tr {
  display: table-row;
}

thead {
  display: table-header-group;
}

tbody {
  display: table-row-group;
}

tfoot {
  display: table-footer-group;
}

col {
  display: table-column;
}

colgroup {
  display: table-column-group;
}

td,
th {
  display: table-cell;
}

caption {
  display: table-caption;
}

th {
  font-weight: bolder;
  text-align: center;
}

caption {
  text-align: center;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

a {
  text-decoration: none;
}

a:focus,
a:active,
a:hover {
  outline: 0;
}

h1, h2, h3, h4, h5, h6,
b, strong {
  font-weight: bolder;
}

img {
  margin: 0;
  padding: 0;
  border: 0;
}

/* Begin bidirectionality settings (do not change) */
BDO[DIR=ltr] {
  direction: ltr;
  unicode-bidi: bidi-override;
}

BDO[DIR=rtl] {
  direction: rtl;
  unicode-bidi: bidi-override;
}

*[DIR=ltr] {
  direction: ltr;
  unicode-bidi: embed;
}

*[DIR=rtl] {
  direction: rtl;
  unicode-bidi: embed;
}

@media print {
  h1 {
    page-break-before: always;
  }

  h1, h2, h3, h4, h5, h6 {
    page-break-after: avoid;
  }

  ul, ol, dl {
    page-break-before: avoid;
  }
}
::-webkit-scrollbar {
  width: 12px;
  height: 8px;
  border: 3px solid #ffffff;
}

::-webkit-scrollbar-button:start:decrement,
::-webkit-scrollbar-button:end:increment {
  display: block;
  height: 10px;
  background: #ffffff;
}

::-webkit-scrollbar-track {
  background: #ffffff;
  -webkit-border-radius: 10px;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  height: 50px;
  width: 50px;
  background: #ff8955;
  -webkit-border-radius: 8px;
  border-radius: 8px;
}

.v-application {
  font-family: "Maplestory", sans-serif !important;
}

@font-face {
  font-family: "Maplestory";
  font-weight: normal;
  font-style: normal;
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.eot");
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.eot?#iefix") format("embedded-opentype"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.woff2") format("woff2"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.woff") format("woff"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryLight.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "Maplestory";
  font-weight: bold;
  font-style: normal;
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.eot");
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.eot?#iefix") format("embedded-opentype"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.woff2") format("woff2"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.woff") format("woff"), url("https://cdn.jsdelivr.net/gh/webfontworld/NexonMaplestory/NexonMaplestoryBold.ttf") format("truetype");
  font-display: swap;
}
div {
  font-family: "Maplestory";
  color: #5e5e5e;
}

a {
  color: #5e5e5e;
}

a:link {
  color: #5e5e5e;
}

a:visited {
  color: #5e5e5e;
}

a:active {
  color: #5e5e5e;
}

.body {
  background: #eee;
  height: 100%;
  min-height: 100vh;
}

input {
  border: 1px solid #e4e4e4;
  height: 30px;
  border-radius: 20px;
}

.datetime {
  width: 90%;
  margin-left: 10%;
}

.reservation_box {
  margin-top: 20px;
  display: flex;
  flex-flow: column;
  margin: auto;
  flex-wrap: wrap;
}
.reservation_box label {
  display: flex;
  flex-flow: column;
  margin: auto;
  width: 60%;
  font-size: 1em;
  line-height: 1.8em;
  margin-top: 20px;
}
.reservation_box input {
  text-align: center;
}

.payment {
  padding-bottom: 20px;
}
.payment h3 {
  line-height: 2em;
  margin: 20px 0 0;
  font-size: 1.4em;
}
.payment span {
  font-size: 1.2em;
  line-height: 2em;
  margin-right: 10px;
  color: #cf5724;
  font-weight: bold;
}

.pay {
  display: block;
  color: #fff;
  margin: auto;
  padding: 20px;
  background: #cf5724;
  border: 1px solid #cf5724;
  font-size: 2em;
}
.pay button {
  width: 100%;
  height: 100%;
  border: 0;
  background: 0;
  color: #fff;
}

.pay:hover {
  background: #ff8955;
}

.Reservation-check:hover,
.Reservation-check:link,
.Reservation-check:active,
.Reservation-check:visited {
  color: #fff;
}

@media all and (max-width: 767px) {
  .reservation {
    background: #fff;
    margin: auto;
  }
  .reservation input {
    width: 70%;
    margin: auto;
  }
  .reservation .pay {
    width: 100%;
  }

  .contents_list {
    width: 100%;
    margin: auto;
    border: 1px solid #eeeeee;
    background-color: #ffffff;
    border-top: 0;
  }

  .contents {
    padding-top: 50px;
    background: #fff;
    border: 1px solid #e4e4e4;
    display: flex;
    margin: auto;
    flex-wrap: wrap;
    flex-flow: column;
  }
  .contents .img_box {
    height: 150px;
    order: 0;
    overflow: hidden;
    background: #fff;
  }
  .contents .img_box img {
    height: 150px;
    width: auto;
  }
  .contents .contents_box {
    display: flex;
    flex-wrap: wrap;
    height: 220px;
    width: 100%;
    padding-left: 20px;
    justify-content: space-between;
  }
  .contents .contents_box .c_box_01 {
    order: 1;
    width: 60%;
    text-align: left;
  }
  .contents .contents_box .c_box_01 .rank_name {
    font-weight: bold;
    line-height: 1.8em;
    font-size: 1.4em;
  }
  .contents .contents_box .c_box_01 dd {
    line-height: 1.4em;
    font-size: 1em;
  }
  .contents .contents_box .c_box_02 {
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    order: 2;
    width: 40%;
    min-width: 70px;
    text-align: center;
  }
  .contents .contents_box .c_box_02 .car_rank {
    padding: 10px 20px;
    display: block;
    color: #fff;
    font-size: 1.2em;
    background: #ff8955;
  }
  .contents .contents_box .c_box_02 .star {
    font-size: 1em;
    margin: 10px 20px;
    color: #ff8955;
    position: relative;
    top: -20px;
  }
  .contents .contents_box .c_box_03 {
    display: none;
  }
  .contents .contents_box .c_box_04 {
    width: 100%;
    order: 4;
    text-align: center;
    display: flex;
    flex-flow: column;
    line-height: 1em;
    font-size: 1em;
    padding-right: 50px;
  }
  .contents .contents_box .c_box_04 span {
    overflow: hidden;
    width: 60px;
    position: relative;
    right: -235px;
    top: -20px;
  }
}
@media all and (min-width: 768px) {
  .reservation {
    background: #fff;
    width: 700px;
    margin: auto;
  }
  .reservation input {
    width: 50%;
    margin: auto;
  }
  .reservation .pay {
    width: 700px;
  }

  .contents {
    padding-top: 59px;
    padding-bottom: 200px;
    background: #fff;
    border: 1px solid #e4e4e4;
    display: flex;
    width: 700px;
    height: 200px;
    margin: auto;
  }
  .contents .img_box {
    width: 250px;
    height: 200px;
    margin-right: 20px;
    order: 0;
    overflow: hidden;
    background: #fff;
  }
  .contents .img_box img {
    width: 100%;
  }
  .contents .contents_box {
    display: flex;
    flex-wrap: wrap;
    height: 200px;
    width: 450px;
    justify-content: space-between;
  }
  .contents .contents_box .c_box_01 {
    order: 1;
    text-align: left;
  }
  .contents .contents_box .c_box_01 .rank_name {
    font-weight: bold;
    font-size: 2em;
    line-height: 2em;
  }
  .contents .contents_box .c_box_01 dd {
    line-height: 1.6em;
    font-size: 1.2em;
  }
  .contents .contents_box .c_box_02 {
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    order: 2;
    text-align: right;
  }
  .contents .contents_box .c_box_02 .car_rank {
    padding: 10px 20px;
    display: block;
    color: #fff;
    font-size: 1.6em;
    background: #ff8955;
    border: 1px solid red;
  }
  .contents .contents_box .c_box_02 .star {
    font-size: 1.4em;
    margin: 10px 20px;
    color: #ff8955;
  }
  .contents .contents_box .c_box_03 {
    position: relative;
    top: -1px;
    order: 3;
    width: 40%;
    text-align: left;
    align-self: flex-start;
    font-size: 3em;
    margin-top: 20px;
    color: #cf5724;
  }
  .contents .contents_box .c_box_04 {
    width: 60%;
    order: 4;
    text-align: center;
    display: flex;
    flex-flow: column;
    align-self: flex-end;
  }
  .contents .contents_box .c_box_04 span {
    overflow: hidden;
    width: 60px;
    position: relative;
    right: -170px;
    top: -20px;
  }
}